import fetch from 'isomorphic-fetch'
import {EventEmitter} from 'events';
import assign from 'object-assign';
//action的类型
export const REQUEST_TOPICS = 'REQUEST_TOPICS';
export const RECEIVE_TOPICS = 'RECEIVE_TOPICS';
export const SELECT_TAB= 'SELECT_TAB';
export const RECORD_SCROLLT='RECORD_SCROLLT';
export const REQUEST_ARTICLE = 'REQUEST_ARTICLE';
export const RECEIVE_ARTICLE = 'RECEIVE_ARTICLE';
export const CHANGE_CURRENT_TOPICID = 'CHANGE_CURRENT_TOPICID';
export const CURRENT_ROUTER = 'CURRENT_ROUTER';
export const LOGIN_SUCCESS = 'LOGIN_SUCCESS';
export const LOGIN_FAILED = 'LOGIN_FAILED';
export const LOGOUT = 'LOGOUT';
export const REQUEST_PROFILE = 'REQUEST_PROFILE';
export const RECEIVE_PROFILE = 'RECEIVE_PROFILE';
export const SWITCH_SUPPORT = 'SWITCH_SUPPORT';
export const FETCH_COMMENT = 'FETCH_COMMENT';
export const SWITCH_COLLECTED = 'SWITCH_COLLECTED';
export const RECORD_ARTICLE_SCROLLT = 'RECORD_ARTICLE_SCROLLT';
export const PUBLISH_TOPIC = 'PUBLISH_TOPIC';
export const FETCH_MESSAGE = 'FETCH_MESSAGE';
export const MARK_ALL_MESSAGES = 'MARK_ALL_MESSAGES';
export const GET_COLLECTED_TOPICS = 'GET_COLLECTED_TOPICS';
//练习

export const TODO_LIST = "TODO_LIST";
export const todoAction = {
    type: TODO_LIST
};
export const TodoStore = assign({}, EventEmitter.prototype, {
    items: {
        todos: [
            { id: 0, name: 'Todo one', done: false },
            { id: 1, name: 'Todo two', done: false },
            { id: 2, name: 'Todo three', done: false },
        ]
    },

    nextId: 3,

    getAll: function getAll() {
        return this.items;
    },

    emitChange: function emitChange() {
        this.emit('change');
    },

    addChangeListener: function addChangeListener(callback) {
        this.on('change', callback);
    },

    removeChangeListener: function removeChangeListener(callback) {
        this.removeListener('change', callback);
    },

    addNewTodo: function addNewTodo(todo) {
        const todos = this.items.todos;
        if (!todos || typeof this.items.todos.length !== 'number') {
            this.items.todos = [];
        }
        todo.id = this.nextId++;
        todo.done = false;
        this.items.todos.push(todo);
    },

    toggleDone: function toggleDone(id) {
        this.items.todos = this.items.todos.map(todo => {
            if (todo.id === id) {
                todo.done = !todo.done;
            }
            return todo;
        });
    },

    deleteTodo: function deleteTodo(id) {
        this.items.todos = this.items.todos.filter((todo) => todo.id !== id);
    }
});


